.information {
  font-size: .75em;
  color: blue;
  cursor: pointer;
}

.wrapper button.information {
   border: none;
}

.information-wrapper {
  position: relative;
}

.preview {
   border: 1px darkgray solid;
   padding: 10px;
}

.wrapper {
   display: flex;
   flex-direction: column;
   padding: 20px;
   text-align: left;
}

.wrapper button {
   background: none;
   border: 1px black solid;
   margin-top: 10px;
}